
<script>
import { METHOD_TYPES } from '@babel/types';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';



gsap.registerPlugin(ScrollTrigger);

export default {

   mounted(){
      this.showMyDiv() ;
      gsap.to(".tc-container",{
        scrollTrigger:{
        trigger: "#about",
        markers: false,
        start: "center center",
        end: "+=400", 
        toggleClass: {targets: ".tc-container", className: "active"},
        },
        duration:0,
        });
      gsap.to(".ticket-flat",{
        scrollTrigger:{
        trigger:  "#about",
        markers: false,
         start: "center center",
        end: "+=400", 
        toggleClass:{targets: ".ticket-flat", className: "active"},
        },
        duration:0,
        });

     gsap.to(".ticketWord",{
        scrollTrigger:{
        trigger:  "#about",
        markers: false,
        start: "top center",
        toggleClass:{targets: ".ticketWord", className: "active"},
        },
        duration:0,
